<main class="content"> <section class="signin"><h2 class="signin_title">LOGIN</h2><div class="signin_form"><div class="signin_inputs"><label class="signin_label" for="email"><span>E-mail</span><input class="signin_input" id="email" type="email" v-model="this.emailValue"></label><label class="signin_label" for="password"><span>Password</span><input class="signin_input" id="password" type="password" v-model="this.passwordValue"></label></div><a class="signin_forgot" @click="$router.push('/find')">Forgot your password?</a><div class="signin_alert">Email is required.</div><button class="signin_submit" @click="submitLogin()">Login</button><div class="signin_or"> <span>Or</span></div><button class="signin_google signin_button"><img src="../img/icon_google.webp"><span>Sign in with Google</span></button><button class="signin_facebook signin_button"><img src="../img/icon_facebook.webp"><span>Sign in with Facebook</span></button><div class="signin_signup"> <span>Dont have an accounting?</span><a @click="$router.push('/sign/up')">Sign up.</a></div></div></section></main>