<div class="popup" @click="closePopup()"><div class="popup_confirm" style="display: none" @click.stop><div class="popup_confirm_title">REVIEW</div><div class="popup_confirm_description">Look like you havent rated previously plotted pattern. Would you like to rate now?</div><div class="popup_confirm_buttons"><button class="popup_button popup_confirm_cancel" @click="closePopup()">Cancel</button><button class="popup_button popup_confirm_rate">Rate now!</button></div></div><div class="popup_check" style="display: none" @click.stop><div class="popup_check_title">CHECK A REVIEW</div><div class="popup_check_review"><div class="popup_check_subtitle">My review</div><div class="popup_check_grade"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_off.webp"></div></div><div class="popup_check_comment"><span>Comment</span><div>The pattern size is correct!!! Next time I change my car, I will buy the pattern this person worked on again! But is there really a pattern in the car I want to change?</div></div><div class="popup_check_buttons"><button class="popup_button popup_check_cancel">Delete</button><button class="popup_button popup_check_confirm" @click="closePopup()">Confirm</button></div></div><div class="popup_window" style="display: none" @click.stop><div class="popup_title">WRITE A REVIEW</div><div class="popup_customer"> <div class="popup_customer_grid"> <div class="popup_customer_title">Customer rating</div><div class="popup_customer_score">4.1</div><div class="popup_customer_grade"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_on.webp"><img src="@/img/icon_star_off.webp"></div><div class="popup_customer_numbers">(70 Customer reviews)</div></div><div class="popup_customer_grid"><div class="popup_customer_title">Customer rating</div><div class="popup_customer_bars"><div class="popup_customer_bar"> <div class="popup_customer_progressbar"><div class="popup_customer_progress" style="height: 87%"></div></div><span>5</span></div><div class="popup_customer_bar"><div class="popup_customer_progressbar"><div class="popup_customer_progress" style="height: 66%"></div></div><span>4</span></div><div class="popup_customer_bar"><div class="popup_customer_progressbar"><div class="popup_customer_progress" style="height: 47%"></div></div><span>3</span></div><div class="popup_customer_bar"><div class="popup_customer_progressbar"><div class="popup_customer_progress" style="height: 27%"></div></div><span>2</span></div><div class="popup_customer_bar"><div class="popup_customer_progressbar"><div class="popup_customer_progress" style="height: 0%"></div></div><span>1</span></div></div></div></div><div class="popup_review"><div class="popup_review_title">My review</div><div class="popup_review_grade"><img src="@/img/icon_star_off.webp"><img src="@/img/icon_star_off.webp"><img src="@/img/icon_star_off.webp"><img src="@/img/icon_star_off.webp"><img src="@/img/icon_star_off.webp"></div></div><div class="popup_comment"><div class="popup_comment_title">Comment</div><textarea class="popup_comment_text_input" type="textarea" placeholder="Enter..." cols="40" rows="7"></textarea></div><div class="popup_buttons"><button class="popup_button popup_button_cancel" @click="closePopup()">Cancel</button><button class="popup_button popup_button_register" @click="registerReview()">Register</button></div></div></div>