<main class="content"><section class="management"><div class="management_title">PERSONAL INFORMATION MANAGEMENT</div><div class="management_nav"><div class="management_nav_item management_nav_item_select management_nav_information" @click="toggleTab($event)">Edit personal information</div><div class="management_nav_item management_nav_subscription" @click="toggleTab($event)">Editor subscription history</div><div class="management_nav_item management_nav_withdrawal" @click="toggleTab($event)">Withdrawal</div></div><div class="management_tab"><div class="management_tab_item management_tab_item_select management_tab_information"><div class="management_personal"> <div class="management_personal_title">EDIT PERSONAL INFORMATION </div><div class="management_personal_form"><div class="management_personal_subtitle">Profile</div><div class="management_personal_profile"><div class="management_personal_account"><img class="management_personal_img" src="../img/img_profile_4.webp"><div><label class="management_personal_file management_personal_button">Upload<input type="file" style="display: none"></label><p class="management_personal_info">You can upload files by PNG, JPG, GIF</p></div></div><label class="management_personal_label management_personal_label_profile" for="nickname"><span>Nick name</span><input class="management_personal_input" id="nickname" type="text" value="mercedesbenzz"></label></div><div class="management_personal_box management_personal_box_cannot"><div class="management_personal_box_subtitle">Email<span>Cannot be modified</span></div><input class="management_personal_box_input" type="email" id="email" value="mercedesbenzz@gmail.com" disabled></div><div class="management_personal_subtitle" style="margin-bottom: 4px">Password</div><div class="management_personal_description">Set at least 8 characters using a combination of English letters, numbers, and special characters</div><div class="management_personal_inputs"><label class="management_personal_label" for="password"><span>Password</span><input class="management_personal_input" id="password" type="password"></label><label class="management_personal_label" for="retype"><span>Retype password</span><input class="management_personal_input" id="retype" type="password"></label></div><div class="management_personal_subtitle">Name<span>Cannot be modified</span></div><div class="management_personal_inputs management_personal_inputs_cannot"><label class="management_personal_label" for="name"><span>First name</span><input class="management_personal_input" id="name" type="text" value="Gil dong" disabled></label><label class="management_personal_label" for="second"><span>Second name</span><input class="management_personal_input" id="second" type="text" value="Hong" disabled></label></div><div class="management_personal_box"><div class="management_personal_box_subtitle">Mobile Phone</div><input class="management_personal_box_input" id="mobile" type="tel" value="+14255550100"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">Company name    </div><input class="management_personal_box_input" id="company" type="text" value="Thunder Data Service co., Ltd"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">Address 1</div><input class="management_personal_box_input" id="address_1" type="text" value="5515 NE 148TH AVE STE 302"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">Address 2</div><input class="management_personal_box_input" id="address_2" type="text" value="312 Cherry Lane"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">Town / City</div><input class="management_personal_box_input" id="city" type="text" value="California"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">State / Province / Region</div><input class="management_personal_box_input" id="state" type="text" value="California"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">ZIP / Postal Code</div><input class="management_personal_box_input" id="postal" type="text" value="90746"></div><div class="management_personal_box"><div class="management_personal_box_subtitle">Country</div><select class="management_personal_box_select" size="auto"><option value="">Europe</option><option value="">Canada</option><option value="" selected>United States of America</option><option value="">South Korea</option></select></div><div class="management_personal_buttons"><button class="management_personal_register">Correction</button></div></div></div></div><div class="management_tab_item management_tab_subscription"><div class="management_subscription"><div class="management_subscription_table"><div class="management_subscription_row management_subscription_row_title"><div>Editor subscription title</div><div>Subscription period</div><div>Payment date</div></div><div class="management_subscription_row"><div>30-DAYS FREE TRIAL</div><div>2021. 12. 01 ~ 2021. 12. 31</div><div>2021. 12. 31</div></div><div class="management_subscription_row"><div>30-DAYS FREE TRIAL</div><div>2021. 11. 01 ~ 2021. 11. 30</div><div>2021. 12. 01</div></div><div class="management_subscription_row"><div>30-DAYS FREE TRIAL</div><div>2021. 10. 01 ~ 2021. 10. 31</div><div>2021. 10. 31</div></div><div class="management_subscription_row"><div>30-DAYS FREE TRIAL</div><div>2021. 09. 01 ~ 2021. 09. 30</div><div>2021. 09. 30</div></div></div></div><div class="management_subscription_button">CANCEL SUBSCRIPTION</div></div><div class="management_tab_item management_tab_withdrawal"><div class="management_withdrawal"><div class="management_withdrawal_title">Delete account</div><div class="management_withdrawal_description">Please read this carefully. Deleting your account is permanent and data loss is irretrievable. <br/> Youre about to submit a request to delete your personal PLOFIX account, which provides access to various patterns. If you delete your account, you will no longer have access to those products and services, all your account data and assets will be lost, and you will no longer be able to log into or restore your account or the data you stored with PLOFIX. All of your data and assets will automatically be scheduled for deletion from PLOFIXs servers.<br/> Additionally, PLOFIX may not be able to provide you with payments you might be expecting, such as reimbursement for a subscription plan or payment for assets youve sold on PLOFIX.</div><div class="management_withdrawal_subtitle">Loss of data and assets</div><div class="management_withdrawal_notice">Here are the consequences of deleting your PLOFIX account:</div><div class="management_withdrawal_text">- You will lose access to PLOFIX services, such as Edit & Plot or Upload and pattern database;</div><div class="management_withdrawal_text">- All your saved projects will be deleted, and unused credit packs will be forfeited;</div><div class="management_withdrawal_text">- All data stored inPLOFIX will be permanently lost;</div><div class="management_withdrawal_text" style="margin-bottom: 47px;">- You wont be able to use your email to sign in toPLOFIX</div><div class="management_withdrawal_subtitle">Payout request</div><div class="management_withdrawal_text" style="margin-bottom: 17px;">- If your earnings make up $30 and above, you may click here to request a payout before deleting your account.</div><div class="management_withdrawal_request">Withdrawal request</div><div class="management_withdrawal_checkbox"> <input class="management_withdrawal_checkbox_input" type="checkbox" id="management_withdrawal"><label for="management_withdrawal">I have read and agree to the terms listed above.</label></div><div class="management_withdrawal_button" @click="openPopup()">WITHDRAWAL</div></div></div></div></section></main><popup-profile></popup-profile>