<main class="content"> <section class="recovery"><h2 class="recovery_title">FIND PASSWORD</h2><div class="recovery_form"><label class="recovery_label"><h3 class="recovery_subtitle">E-mail</h3><input class="recovery_input" id="email" v-model="this.emailValue"><button class="recovery_button recovery_button_send" @click="sendCode()">send</button></label><label class="recovery_label"><h3 class="recovery_subtitle">Verification code</h3><input class="recovery_input" id="code"><button class="recovery_button">Verify</button></label><div class="recovery_alert">Email is required.</div><button class="recovery_submit">Next</button></div></section></main>