<div class="protections_pattern_card" v-for="card in cards" :key="card.id" @click="$router.push(`/card/${card.id}`)"><img class="protections_pattern_card_bedge" v-if="card.bedge === true" src="@/img/bedge.webp"><div class="protections_pattern_date"> <span>Upload date : {{ card.upload }}</span><div class="protections_pattern_mark protections_pattern_mark_active" v-if="card.markActive === true"><img src="@/img/mark.webp"></div><div class="protections_pattern_mark" v-if="card.markActive === false"><img src="@/img/mark.webp"></div></div><img class="protections_pattern_pattern" src="@/img/img_pattern.webp"><div class="protections_pattern_subtitle">{{ card.subtitle }}</div><div class="protections_pattern_path">OUT FRONT &gt; BUMPER</div><div class="protections_pattern_info"><div class="protections_pattern_grade" v-if="card.grade === 1"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_off.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_off.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_off.webp"><img class="protections_pattern_star protections_pattern_star_off" src="@/img/icon_star_off.webp"></div><div class="protections_pattern_grade" v-if="card.grade === 2"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_off.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_off.webp"><img class="protections_pattern_star protections_pattern_star_off" src="@/img/icon_star_off.webp"></div><div class="protections_pattern_grade" v-if="card.grade === 3"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_off.webp"><img class="protections_pattern_star protections_pattern_star_off" src="@/img/icon_star_off.webp"></div><div class="protections_pattern_grade" v-if="card.grade === 4"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_off" src="@/img/icon_star_off.webp"></div><div class="protections_pattern_grade" v-if="card.grade === 5"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_on" src="@/img/icon_star_on.webp"><img class="protections_pattern_star protections_pattern_star_off" src="@/img/icon_star_on.webp"></div><div class="protections_pattern_count"><div class="protections_pattern_credit">{{ card.credit }}</div><div class="protections_pattern_downloads">{{ card.downloads }}</div></div></div><div class="protections_pattern_users"><div class="protections_pattern_user"><img :src="card.userImage"><span>By {{ card.userName }}</span></div><div class="protections_pattern_status"><img :src="card.statusImage"><span>{{ card.statusName }}</span></div></div></div>