<main class="content"> <section class="signup"><h2 class="signup_title">Sign Up</h2><h3 class="signup_subtitle">Welcome to your Ultrafit Registration</h3><div class="signup_form"><h3 class="signup_form_subtitle">Profile</h3><div class="signup_form_profile"><div class="signup_form_account"><img class="signup_form_img" src="../img/icon_account.svg"><div><label class="signup_form_file signup_form_button">Upload<input type="file" style="display: none" onchange="uploadImg()"></label><p class="signup_form_info">You can upload files by PNG, JPG, GIF</p></div></div><label class="signup_label signup_label_profile" for="signup_nickname"><span>Nick name</span><input class="signup_input" id="signup_nickname" type="text"><p class="signup_alert">Enter user name.</p></label></div><div class="signup_box"><h3 class="signup_box_subtitle">Email</h3><input class="signup_box_input" type="email" id="signup_email"><p class="signup_alert">Enter Email.</p><button class="signup_box_button">send</button></div><div class="signup_box"><h3 class="signup_box_subtitle">Verification code</h3><input class="signup_box_input" id="signup_code" type="text"><p class="signup_alert signup_alert_verify">Please confirm verification code.</p><button class="signup_box_button signup_box_button_verify">Verify</button></div><h3 class="signup_form_subtitle" style="margin-bottom: 4px">Password</h3><h4 class="signup_form_description">Set at least 8 characters using a combination of English letters, numbers, and special characters</h4><div class="signup_inputs"><label class="signup_label" for="signup_password"><span>Password</span><input class="signup_input" id="signup_password" type="password"></label><label class="signup_label" for="signup_retype"><span>Retype password</span><input class="signup_input" id="signup_retype" type="password"><p class="signup_alert signup_alert_password">Please confirm password.</p></label></div><h3 class="signup_form_subtitle">Name</h3><div class="signup_inputs"><label class="signup_label" for="signup_name"><span>First name</span><input class="signup_input" id="signup_name" type="text"></label><label class="signup_label" for="signup_second"><span>Second name</span><input class="signup_input" id="signup_second" type="text"><p class="signup_alert">Enter name.</p></label></div><div class="signup_box"><h3 class="signup_box_subtitle">Mobile Phone</h3><input class="signup_box_input" id="signup_mobile" type="tel"><p class="signup_alert">Enter mobile phone.</p></div><div class="signup_box"><h3 class="signup_box_subtitle">Company name    </h3><input class="signup_box_input" id="signup_company" type="text"><p class="signup_alert">Enter company.</p></div><div class="signup_box"><h3 class="signup_box_subtitle">Address 1</h3><input class="signup_box_input" id="signup_address_1" type="text"><p class="signup_alert">Enter address 1.</p></div><div class="signup_box"><h3 class="signup_box_subtitle">Address 2</h3><input class="signup_box_input" id="signup_address_2" type="text"><p class="signup_alert">Enter address 2.</p></div><div class="signup_box"><h3 class="signup_box_subtitle">Town / City</h3><input class="signup_box_input" id="signup_city" type="text"><p class="signup_alert">Enter town / city</p></div><div class="signup_box"><h3 class="signup_box_subtitle">State / Province / Region</h3><input class="signup_box_input" id="signup_state" type="text"><p class="signup_alert">Enter state / province / region</p></div><div class="signup_box"><h3 class="signup_box_subtitle">ZIP / Postal Code</h3><input class="signup_box_input" id="signup_postal" type="text"><p class="signup_alert">Enter zip / postal Code</p></div><div class="signup_box"><h3 class="signup_box_subtitle">Country</h3><select class="signup_box_select" size="auto"><option class="signup_box_option" disabled selected>Enter...</option><option value="">Europe</option><option value="">Canada</option><option value="">United States</option><option value="">South Korea</option></select><p class="signup_alert">Choose country</p></div><div class="signup_buttons"><button class="signup_cancel">Cancel</button><button class="signup_register" @click="register()">Register</button></div></div></section></main>