<main class="content"><section class="pricing"><div class="pricing_title">Pricing</div><div class="pricing_subtitle">Choose credit plan designed to meet your needs.</div><div class="pricing_buttons"><button class="pricing_button pricing_button_credit pricing_button_active" @click="toogleTab($event)">Credit packs</button><button class="pricing_button pricing_button_subscription" @click="toogleTab($event)">EDIT & PLOT Subscription</button></div></section><section class="credit"><div class="credit_title">CREDIT PACKS</div><div class="credit_subtitle">Buy credits to download patterns</div><credit-list style="grid-gap: 20px;"></credit-list><div class="credit_attention">Please select the desired credit pack. (Duplicate selection is possible)</div><div class="credit_choice">Total : $0</div><button class="credit_button_paypal"><img src="@/img/logo_paypal.svg"></button></section><section class="subscription" style="display: none"><div class="subscription_title">EDIT & PLOT SUBSCRIPTION</div><div class="subscription_subtitle">Use editor to plot downloaded patterns</div><div class="subscription_option"> <div><input class="subscription_checkbox" type="checkbox" id="subscription_checkbox_1"><label for="subscription_checkbox_1">30-DAYS FREE TRIAL</label></div><div class="subscription_right"><div class="subscription_text">Total Credit</div><div> <div class="subscription_price">$30<span>/mo</span></div><span class="subscription_notice">*VAT not included in prices.</span></div></div></div><button class="credit_button_paypal"><img src="@/img/logo_paypal.svg"></button></section></main>