<div class="popup" @click="closePopup()"><div class="popup_withdrawal" style="display: none" @click.stop><div class="popup_withdrawal_title">WITHDRAWAL</div><div class="popup_withdrawal_description">Are you sure you want to delete your account?</div><div class="popup_withdrawal_buttons"><button class="popup_button popup_withdrawal_no" @click="closePopup()">No</button><button class="popup_button popup_withdrawal_yes" @click="deleteProfile()">Yes</button></div></div></div>