<main class="content"><section class="upload"><div class="upload_head"><h2 class="upload_title">UPLOAD PATTERN</h2><div class="upload_subtitle">You can upload up to 5 patterns for the selected car model.</div></div><div class="upload_form"><div class="upload_form_head"><div class="upload_form_title">Category</div><div class="upload_form_subtitle"> Please select all the required fields</div><div class="upload_form_button" @click="$router.push('/register/car')">Register new car</div></div><div class="search"><search-upload></search-upload></div><details class="upload_pattern" open><summary class="upload_pattern_summary"><span class="upload_pattern_title">Pattern 1.</span></summary><div class="upload_pattern_dropdown"><div class="upload_pattern_subtitle">Sub Category</div><filter-upload></filter-upload><div class="upload_pattern_subtitle">Price Per Area</div><div class="upload_pattern_price"><div class="upload_pattern_credit">0.5 Credit</div><div class="upload_pattern_credit">1.0 Credit</div><div class="upload_pattern_credit">1.5 Credit</div><div class="upload_pattern_credit">2.0 Credit</div></div><div class="upload_pattern_subtitle">Upload file</div><div class="upload_pattern_file"><span class="upload_pattern_file_subtitle">Drag and Drop files or Browser</span><input class="upload_pattern_file_input" type="file" name="files" id="upload_pattern_file_input" @change="uploadFile()"><label class="upload_pattern_file_label" for="upload_pattern_file_input"><span class="upload_pattern_file_text">Upload</span></label><div class="upload_pattern_file_uploader"><span>Pattern.svg</span><img src="@/img/remove.svg" @click="removeFile()"></div></div><div class="upload_pattern_subtitle">Description</div><textarea class="upload_pattern_description" placeholder="Enter..."></textarea><div class="upload_pattern_subtitle" style="margin-bottom: 4px">Tag</div><div class="upload_form_subtitle">*Up to 5 tags can be registered.</div><tags-upload></tags-upload></div></details><div class="upload_button"> <button>Upload pattern</button></div><div class="upload_custom"> <div class="upload_custom_head"> <div class="upload_pattern_subtitle" style="margin-bottom: 5px">Custom Coverage</div><div class="upload_form_subtitle">See the pattern that you have uploaded for the specific part of the car.</div></div><div class="upload_custom_car"> <img class="upload_custom_car_line" src="@/img/car_line.svg"><img class="upload_custom_car_piece" src="@/img/piece.svg"></div></div><div class="upload_approve"><button>Approval request </button></div></div></section></main>