<main class="content"><section class="product"><div class="product_head"><div class="product_image"><img src="@/img/img_thumb_8.webp"></div><div class="product_options"><div class="product_options_title">ULTRAFIT VF</div><div class="product_options_subtitle">Window Film</div><div class="product_options_option">Option</div><details class="product_select"><summary>Size</summary><div class="product_dropbox"><div class="product_select_option">List 1</div><div class="product_select_option">List 2</div><div class="product_select_option">List 3</div></div></details><div class="product_options_credit">20 Credit</div><div class="product_options_buttons"><button class="product_options_button product_options_button_add">Add to Cart</button><button class="product_options_button">Buy</button></div></div></div><div class="product_nav"><div class="product_nav_button product_nav_button_info product_nav_button_select" @click="toogleTab($event)">Information</div><div class="product_nav_button product_nav_button_review" @click="toogleTab($event)">Reviews</div></div><div class="product_list"><div class="product_list_item product_list_info product_list_item_select"><div class="product_info"><div class="product_info_title">Why Ultrafit VC?</div><div class="product_info_subtitle">Permanent Gratification</div><div class="product_info_description">Chosen as one of the most popular selections of metallic window films in the industry, experoence the comfortability and timeless features of the Vivid Comfort window film today.</div><img class="product_info_img" src="@/img/img_info_card.png"></div></div><div class="product_list_item product_list_review"><div class="product_reviews"><div class="product_reviews_head"><div class="product_reviews_account"><div class="product_reviews_account_img"><img src="@/img/img_profile_4.webp"></div><div class="product_reviews_account_nickname">mercedesbenzz</div></div><button class="product_reviews_register">Register</button></div><div class="product_reviews_text"><textarea class="product_reviews_textarea" cols="30" rows="10" placeholder="Leave a product review after your purchase"></textarea></div><div class="product_reviews_list"><div class="product_reviews_subtitle">Reviews (15)</div><div class="product_reviews_items"><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_13.webp"></div><div class="product_reviews_item_nickname">Steve sole</div></div><div class="product_reviews_item_ago">3days ago</div></div><div class="product_reviews_item_comment">Ive been looking for a lot of sites, but Ive found the product I want here!</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_12.webp"></div><div class="product_reviews_item_nickname">Lovecar</div></div><div class="product_reviews_item_ago">8days ago</div></div><div class="product_reviews_item_comment">It has been 5 years since I used this product! Thank you for always making great products. You will know once you use this product! Really good. I will continue to purchase in the future!</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_11.webp"></div><div class="product_reviews_item_nickname">Robert</div></div><div class="product_reviews_item_ago">11days ago</div></div><div class="product_reviews_item_comment">The 12x48 is a good size to finish most headlights. I used them to cover refreshly polished headlights on my S2000. The film was thick and adhesion was strong.</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_10.webp"></div><div class="product_reviews_item_nickname">Bwming</div></div><div class="product_reviews_item_ago">13days ago</div></div><div class="product_reviews_item_comment">It seems like good quality PPF to me. Everyone warned me it would be hard to install. Was not the case - I just took my time and it was easy. Installed it on my WRX with only about 85 miles on it, so the clean, new paint probably helped. Its a little thinner than I expected, but I guess that helps with it being less visible.</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_9.webp"></div><div class="product_reviews_item_nickname">Ultra car</div></div><div class="product_reviews_item_ago">20days ago</div></div><div class="product_reviews_item_comment">So far, very happy with how this adhered (cleaned window and applied on a hot day) and is holding up. I hope it will continue to last.</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_8.webp"></div><div class="product_reviews_item_nickname">Mike</div></div><div class="product_reviews_item_ago">22days ago</div></div><div class="product_reviews_item_comment">The 12x48 is a good size to finish most headlights. I used them to cover refreshly polished headlights on my S2000. The film was thick and adhesion was strong.</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_7.webp"></div><div class="product_reviews_item_nickname">carlover</div></div><div class="product_reviews_item_ago">30days ago</div></div><div class="product_reviews_item_comment">Excellent product! Take your time. Clean the area being protected. Trim to your desired size needed. Apply soapy water (Dawn) from a spray bottle to the vehicle and vinyl. Squeegee the water and bubbles out. Use a heat gun to shrink and clean up around curves and edges.</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_6.webp"></div><div class="product_reviews_item_nickname">Jason collins</div></div><div class="product_reviews_item_ago">31days ago</div></div><div class="product_reviews_item_comment">Its basically a sample size, product is ok just disappointed with the amount.</div></div><div class="product_reviews_item"><div class="product_reviews_item_head"><div class="product_reviews_item_account"><div class="product_reviews_item_img"><img src="@/img/img_profile_5.webp"></div><div class="product_reviews_item_nickname">Best driver</div></div><div class="product_reviews_item_ago">33days ago</div></div><div class="product_reviews_item_comment">I let it dry over night and my saddle bags on my bike is very protected.</div></div></div><button class="product_reviews_load">LOAD MORE</button></div></div></div></div></section></main>